.blog-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-top: 1rem;
}
.blog-container .blog {
  display: flex;
  position: relative;
  background-color: rgba(var(--basalt-secondary-color), 0.3);
  border: 2px solid rgb(var(--basalt-secondary-color));
  box-sizing: border-box;
  transition: background-color 0.2s linear;
  padding: 0.5rem;
  gap: 0.5rem;
}
.blog-container .blog:is(:hover, :focus, :focus-within) {
  background-color: rgba(var(--basalt-secondary-color), 0.7);
}
.blog-container .blog .blog-text {
  flex-basis: 65%;
}
.blog-container .blog .blog-text .blog-title {
  font-size: 1.5rem;
  font-weight: bold;
}
.blog-container .blog .blog-text .blog-time {
  font-size: 0.8rem;
  color: rgba(var(--basalt-main-text-color), 0.5);
}
.blog-container .blog .blog-text .blog-description p:first-of-type {
  margin-top: 0;
}
.blog-container .blog .blog-text .blog-description p:last-of-type {
  margin-bottom: 0;
}
.blog-container .blog .blog-image {
  flex-basis: 35%;
}
.blog-container .blog .blog-link a {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}